/**
 * 家园界面
 * @author Radish
 */

import { INCREASE_DURATION, PROP_ID_MONEY } from "../../../../fairyland/Consts";
import State from "../../../../fairyland/State";
import {
  addProp,
  producePlaceProps,
} from "../../../../fairyland/system/HandlePropSystem";
import { loadPrefab } from "../../../../sys/Utils";
import { save } from "../../../system/LocalStorageSystem";
import PlacePropsToolbar from "../home/PlacePropsToolbar";

const { ccclass, property } = cc._decorator;
const MASK_OPACITY = 128;

@ccclass
export default class HomeComponent extends cc.Component {
  @property(cc.Node)
  public maskNode: cc.Node = null;

  private m_toolbarNode: cc.Node = null;

  public async onLoad() {
    this.maskNode.opacity = MASK_OPACITY;
    this.maskNode.active = true;

    // toolbar
    const resource = await loadPrefab("prefabs/home/PlacePropsToolbar");
    if (resource) {
      const node = cc.instantiate(resource);
      this.node.getChildByName("toolbar").addChild(node);
      this.m_toolbarNode = node;
      this.m_toolbarNode.getComponent(PlacePropsToolbar).init(
        {
          onHideHandle: () => {
            this.hideToolbar();
          },
          onShowHandle: () => {
            this.showToolbar();
          },
        },
        State.getInstance(),
      );
    }

    this.maskNode.active = false;
  }

  public update(dt: number) {
    producePlaceProps(State.getInstance());
  }

  /**
   * 显示资源配置栏
   * @author Radish
   */
  public showToolbar() {
    this.m_toolbarNode.getComponent(PlacePropsToolbar).show((duration) => {
      this.maskNode.active = true;
      this.maskNode.opacity = 0;
      this.maskNode.runAction(
        cc.fadeTo(duration, MASK_OPACITY).easing(cc.easeSineOut()),
      );
    });
  }

  /**
   * 隐藏资源配置栏
   * @author Radish
   */
  public hideToolbar() {
    this.m_toolbarNode.getComponent(PlacePropsToolbar).hide((duration) => {
      const seq = cc.sequence([
        cc.fadeTo(duration, 0).easing(cc.easeSineOut()),
        cc.callFunc(() => {
          this.maskNode.active = false;
        }),
      ]);
      this.maskNode.runAction(seq);
    });
  }

  /**
   * 点击山门按钮
   * @author Radish
   */
  public onClickShanMen() {
    save(State.getInstance());
  }

  /**
   * 点击丹炉按钮
   * @author Radish
   */
  public onClickDanLu() {
    addProp(State.getInstance(), PROP_ID_MONEY, "1");
  }
}
