/**
 * 在 demo 中, 劳动力的处理方式
 * @author Radish
 */

import Big from "big.js";
import State from "../State";

function add(state: State, route: string, addend: string): string {
  const num: Big = new Big(state.get(route));
  const result = num.plus(addend).toString();
  state.set(route, result);
  return result;
}

function isCanSub(state: State, route: string, subtractor: string): boolean {
  const num: Big = new Big(state.get(route));
  return num.gte(subtractor);
}

function sub(state: State, route: string, subtractor: string): boolean {
  if (isCanSub(state, route, subtractor)) {
    const num: Big = new Big(state.get(route));
    state.set(route, num.minus(subtractor).toString());
    return true;
  }
  return false;
}

/**
 * 获得当前劳动力的数量
 * @author Radish
 */
export function getLaborForce(state: State): string {
  return state.get("laborForce", "0");
}

/**
 * 获得当前劳动力的最大值
 * @author Radish
 */
export function getMaxLaborForce(state: State): string {
  return state.get("maxLaborForce", "0");
}

/**
 * 增加当前劳动力
 * @return 返回加法运算之后的结果
 * @author Radish
 */
export function addLaborForce(state: State, addend: string): string {
  return add(state, "laborForce", addend);
}

/**
 * 判断是否能够减少当前劳动力总数
 * @returns true 可减, false 不可减
 * @author Radish
 */
export function isCanSubLaborForce(state: State, subtractor: string): boolean {
  return isCanSub(state, "laborForce", subtractor);
}

/**
 * 减少当前劳动力
 * @return 如果减掉 subtractor 小于 0, 那么返回 false, 表示减法运算不成功; 否则返回 true.
 * @author Radish
 */
export function subLaborForce(state: State, subtractor: string): boolean {
  return sub(state, "laborForce", subtractor);
}

/**
 * 增加劳动力上限
 * @author Radish
 */
export function addMaxLaborForce(
  state: State,
  addend: string,
): { laborForce: string; maxLaborForce: string } {
  const laborForce = add(state, "laborForce", addend);
  const maxLaborForce = add(state, "maxLaborForce", addend);
  return { laborForce, maxLaborForce };
}

/**
 * 判断是否能够减少劳动力上限
 * @author Radish
 */
export function isCanSubMaxLaborForce(
  state: State,
  subtractor: string,
): boolean {
  const laborForce = getLaborForce(state);
  const maxLaborForce = getMaxLaborForce(state);
  const result = laborForce === maxLaborForce;
  if (result) {
    return isCanSub(state, "maxLaborForce", subtractor);
  }
  return result;
}

/**
 * 判断是否能够减少劳动力上限
 * @author Radish
 */
export function subMaxLaborForce(state: State, subtractor: string): boolean {
  if (isCanSubMaxLaborForce(state, subtractor)) {
    sub(state, "laborForce", subtractor);
    sub(state, "maxLaborForce", subtractor);
    return true;
  }
  return false;
}
