const { ccclass, property } = cc._decorator;

import * as _ from "lodash";

interface IMonster {
  name: string;
  hp: number;
  atk: number;
}

@ccclass
export default class DemoBattleMain extends cc.Component {
  @property([cc.Node])
  public monsterViews: cc.Node[] = [];

  @property(cc.Node)
  public monsterListView: cc.Node = null;

  private m_monsterGroups: IMonster[][] = [[], [], []];
  private m_monsters: IMonster[] = [];

  public onLoad() {
    this.genMonsters();
    this.updateMonsterListView();
  }

  /**
   * 生成单个怪物对象
   * @author Radish
   */
  private genMonster(): IMonster {
    return {
      atk: 1,
      hp: _.random(1, 100, false),
      name: "鸭嘴兽",
    };
  }

  /**
   * 生成怪物对象集合
   * @author Radish
   */
  private genMonsters() {
    const max = _.random(20, 50, false);
    for (let i = 0; i < max; ++i) {
      this.m_monsters.push(this.genMonster());
    }
    this.m_monsters.sort((a, b) => {
      return a.hp - b.hp;
    });
  }

  /**
   * 降怪物分组
   * @author Radish
   */
  private divideIntoGroups() {
    this.m_monsterGroups = [[], [], []];
    const split = Math.floor(this.m_monsters.length / 3);
    for (let i = 0; i < 3; ++i) {
      let limit = split * (i + 1);
      if (i === 2) {
        limit = this.m_monsters.length;
      }

      for (let j = split * i; j < limit; ++j) {
        this.m_monsterGroups[i].push(this.m_monsters[j]);
      }
    }
  }

  /**
   * 点击攻击
   * @author Radish
   */
  private onClickAttack() {
    for (const group of this.m_monsterGroups) {
      let damage = 100;
      for (const m of group) {
        if (damage > 0) {
          if (m.hp >= damage) {
            m.hp -= damage;
            damage = 0;
          } else {
            damage -= m.hp;
            m.hp = 0;
          }
        }

        if (damage === 0) {
          break;
        }
      }
    }

    this.updateMonsterViews();
  }

  /**
   * 点击怪物列表
   * @author Radish
   */
  private onClickMonsterList() {
    this.monsterListView.active = true;
  }

  private onClickCloseMonsterListView() {
    this.monsterListView.active = false;
  }

  /**
   * 点击拆分
   * @author Radish
   */
  private onClickDivide() {
    this.divideIntoGroups();
    this.updateMonsterViews();
  }

  /**
   * 更新妖怪视图
   * @author Radish
   */
  private updateMonsterViews() {
    for (const index in this.m_monsterGroups) {
      if (this.m_monsterGroups.hasOwnProperty(index)) {
        const view = this.monsterViews[index];
        const labelNode = view.getChildByName("label");
        const label = labelNode.getComponent(cc.Label);

        const group = this.m_monsterGroups[index];
        if (group.length === 0) {
          label.string = "-";
          continue;
        }

        let hp = 0;
        let count = 0;
        let name = "";
        for (const m of group) {
          if (name === "") {
            name = m.name;
          }

          hp += m.hp;
          if (m.hp > 0) {
            ++count;
          }
        }

        label.string = `${name}\n血量: ${hp}\n数量: ${count}`;

        if (count === 0) {
          labelNode.color = cc.Color.RED;
        } else {
          labelNode.color = cc.Color.BLACK;
        }
      }
    }
  }

  /**
   * 更新
   */
  private updateMonsterListView() {
    const view = this.monsterListView.getChildByName("view");
    const content = view.getChildByName("content");
    content.setContentSize(cc.winSize.width, cc.winSize.height);
    content.removeAllChildren();

    let height = 0;
    for (const i in this.m_monsters) {
      if (this.m_monsters.hasOwnProperty(i)) {
        const m = this.m_monsters[i];
        const node = new cc.Node();
        if (m.hp > 0) {
          node.color = cc.Color.BLACK;
        } else {
          node.color = cc.Color.RED;
        }
        node.opacity = 255;
        node.anchorY = 1.0;
        node.y = -i * 32;
        height += 32;

        const label = node.addComponent(cc.Label);
        label.fontSize = 24;
        label.string = `=>${i + 1}, ${m.name}, hp = ${m.hp}`;

        content.addChild(node);
      }
    }

    if (content.getContentSize().height < height) {
      content.setContentSize(cc.winSize.width, height);
    }
  }
}
