/**
 * 大厅劳动力信息展示
 * @author Radish
 */

const { ccclass, property } = cc._decorator;

@ccclass
export default class LaborInfo extends cc.Component {
  @property(cc.Label)
  public label: cc.Label = null;

  /**
   * 设置劳动力的展示
   * @author Radish
   */
  public setLabor(cur: string, max: string) {
    this.label.string = `${cur}/${max}`;
  }
}
