import Big from "big.js";
import { PLACE_PROP_ID } from "../Consts";
import State from "../State";
import { IProp, IPropConfig } from "../Types";

/**
 * 获得道具的路由键
 * @author Radish
 */
export function getPropKey(propId: string) {
  return "props" + `[${propId}]`;
}

/**
 * 获得默认的道具数据对象
 * @return 返回实际的实例引用
 * @author Radish
 */
export function getDefaultProp(propId: string): IProp {
  const timestamp = Math.floor(Date.now() / 1000);
  return {
    id: propId,
    laborForce: "0",
    lastNum: "0",
    num: "0",
    startProduceTimestamp: timestamp,
  };
}

/**
 * 设置 prop
 * @author Radish
 */
export function setProp(state: State, prop: IProp) {
  const route = getPropKey(prop.id);
  state.set(route, prop);
}

/**
 * 获得 prop
 * @returns 实例副本, 浅拷贝
 * @author Radish
 */
export function getProp(state: State, propId: string): IProp {
  const route = getPropKey(propId);
  const prop = state.get(route, getDefaultProp(propId));
  return Object.assign({}, prop);
}

/**
 * 增加资源
 * @author Radish
 */
export function addProp(state: State, propId: string, num: string) {
  if (num === "0") {
    return;
  }

  const prop = getProp(state, propId);
  prop.lastNum = prop.num;
  prop.num = new Big(prop.num).plus(num).toString();
  setProp(state, prop);
}

/**
 * 减少资源
 * @author Radish
 */
export function subProp(state: State, propId: string, num: string) {
  if (num === "0") {
    return;
  }

  const prop = getProp(state, propId);
  prop.lastNum = prop.num;
  prop.num = new Big(prop.num).sub(num).toString();
  setProp(state, prop);
}

/**
 * 放置资源生产
 * @author Radish
 */
export function producePlaceProp(state: State, propId: string) {
  const config = state.configs.props[propId];
  const prop = getProp(state, propId);
  if (prop.laborForce <= 0) {
    return;
  }

  const duration = Math.floor(Date.now() / 1000) - prop.startProduceTimestamp;
  if (duration <= 0) {
    return;
  }

  const steps: number = Math.floor(duration / config.place.duration);
  if (steps <= 0) {
    return;
  }

  prop.lastNum = prop.num;
  prop.num = new Big(steps)
    .mul(config.place.numPerLaborForce)
    .mul(prop.laborForce)
    .add(prop.num)
    .toString();
  prop.startProduceTimestamp = parseInt(
    new Big(prop.startProduceTimestamp)
      .add(new Big(steps).mul(config.place.duration))
      .toString(),
    10,
  );
  setProp(state, prop);
}

/**
 * 生产全部放置资源
 * @author Radish
 */
export function producePlaceProps(state: State) {
  PLACE_PROP_ID.forEach((value) => {
    producePlaceProp(state, value);
  });
}

