/**
 * 初始化 State.origin
 * @author Radish
 */

import { IOrigin } from "../../fairyland/Types";

/**
 * 初始化 Origin 实例
 * @author Radish
 */
export function initOrigin(origin: IOrigin): IOrigin {
  for (const k in origin.props) {
    if (origin.props.hasOwnProperty(k)) {
      const prop = origin.props[k];
      if (!prop.startProduceTimestamp) {
        prop.startProduceTimestamp = Math.floor(Date.now() / 1000);
      }
    }
  }
  return origin;
}
