const { ccclass } = cc._decorator;

import App from "../../../../App";
import * as Utils from "../../../../sys/Utils";

const LoginPrefabPath = "prefabs/Login";

@ccclass
export default class MainComponent extends cc.Component {
  public async onLoad() {
    App.getInstance().main = this.node;

    const resource = await Utils.loadPrefab(LoginPrefabPath);
    const loginNode = cc.instantiate(resource);
    App.getInstance().gameContainer.addChild(loginNode as cc.Node);
  }
}
