/**
 * Native 回调管理相关
 * @author Radish
 */

let s_session: number = 0;
const s_callbacks: { [s: number]: (value: string) => void } = {};

export default class NativeResponse {
  /**
   * 添加回调函数
   * @author Radish
   */
  public static push(callback: (value: string) => void): number {
    const session = ++s_session;
    s_callbacks[session] = callback;
    return session;
  }
}

if (cc.sys.isNative) {
  if (!jsb.onNativeResponse) {
    jsb.onNativeResponse = (
      session: number,
      value?: string,
      isRemove: boolean = true,
    ) => {
      if (value === null || value === undefined) {
        delete s_callbacks[session];
      } else {
        const callback = s_callbacks[session];
        if (callback) {
          callback(value);

          if (isRemove) {
            delete s_callbacks[session];
          }
        }
      }
    };
  }
}
