/**
 * 分配劳动力给素材的相关功能
 * @author Radish
 */

import Big from "big.js";
import State from "../State";
import {
  addLaborForce,
  getLaborForce,
  subLaborForce,
} from "./DeployLaborForceSystem";
import { getProp } from "./HandlePropSystem";

/**
 * 给素材分配劳动力
 * @returns true 操作成功, false 操作失败
 * @author Radish
 */
export function addLaborForceToProp(
  state: State,
  propId: string,
  addedLaborForce: string,
): boolean {
  const prop = getProp(state, propId);
  const lastLaborForce = prop.laborForce;
  const bigCurrentLaborForce = new Big(getLaborForce(state));
  const bigAddedLaborForce = new Big(addedLaborForce);
  if (bigCurrentLaborForce.gte(bigAddedLaborForce)) {
    prop.laborForce = bigAddedLaborForce.add(prop.laborForce).toString();

    // 当劳动力从无变有的时候, 开始计时
    if (new Big(lastLaborForce).eq(0) && new Big(prop.laborForce).gt(0)) {
      prop.startProduceTimestamp = Math.round(Date.now() / 1000);
    }

    state.set("props[" + prop.id + "]", prop);
    subLaborForce(state, addedLaborForce);
    return true;
  }
  return false;
}

/**
 * 给素材减少劳动力
 * @author Radish
 */
export function subLaborForceFromProp(
  state: State,
  propId: string,
  subedLaborForce: string,
): boolean {
  const prop = getProp(state, propId);
  const bigPropCurrentLaborForce = new Big(prop.laborForce);
  if (bigPropCurrentLaborForce.gte(subedLaborForce)) {
    prop.laborForce = bigPropCurrentLaborForce
      .minus(subedLaborForce)
      .toString();
    state.set("props[" + prop.id + "]", prop);
    addLaborForce(state, subedLaborForce);
    return true;
  }
  return false;
}
