/**
 * Demo 登录界面
 * @author Radish
 */

const { ccclass, property } = cc._decorator;

import App from "../../../../App";
import State from "../../../../fairyland/State";
import * as Utils from "../../../../sys/Utils";
import { initConfigs } from "../../../system/InitConfigSystem";
import {
  getStorageAccount,
  load,
  save,
  setStorageAccount,
} from "../../../system/LocalStorageSystem";

@ccclass
export default class LoginComponent extends cc.Component {
  @property(cc.EditBox)
  public eBoxAccountInput: cc.EditBox = null;

  public async onLoad() {
    await initConfigs(State.getInstance());

    const account = getStorageAccount();
    if (account) {
      this.eBoxAccountInput.string = account;
      await load(account, State.getInstance());
      this.enterHome();
    }
  }

  /**
   * 点击登录
   * @author Radish
   */
  public async onLoginClick() {
    if (this.eBoxAccountInput.string === "" || !this.eBoxAccountInput.string) {
      App.getInstance().showAlert("用户名不能为空");
      return;
    }

    const account = this.eBoxAccountInput.string.trim();
    State.getInstance().set("user", {
      account,
      id: "0",
      name: account,
    });
    setStorageAccount(account);
    save(State.getInstance());

    await this.enterHome();
  }

  /**
   * 进入家园界面
   * @author Radish
   */
  private async enterHome() {
    // const resource = await Utils.loadPrefab("prefabs/DemoHome");
    const resource = await Utils.loadPrefab("prefabs/Home");
    const homeNode = cc.instantiate(resource);
    App.getInstance().gameContainer.addChild(homeNode as cc.Node);
    this.node.destroy();
  }
}
