/**
 * 分析统计 SDK 调用接口
 * @author Radish
 */

export default class AnalyticsUtils {
  /**
   * 页面呈现时调用
   * @author Radish
   */
  public static trackPageAppear(page: string) {
    if (cc.sys.isNative && cc.sys.os === cc.sys.OS_IOS) {
      jsb.reflection.callStaticMethod(
        "AnalyticsUtils",
        "trackPageAppear:",
        JSON.stringify({ page }),
      );
    }
  }

  /**
   * 页面关闭时调用
   * @author Radish
   */
  public static trackPageDisappear(page: string) {
    if (cc.sys.isNative && cc.sys.os === cc.sys.OS_IOS) {
      jsb.reflection.callStaticMethod(
        "AnalyticsUtils",
        "trackPageDisappear:",
        JSON.stringify({ page }),
      );
    }
  }

  /**
   * 追踪事件
   * @author Radish
   */
  public static trackEvent(name: string, properties?: { [k: string]: string }) {
    if (cc.sys.isNative && cc.sys.os === cc.sys.OS_IOS) {
      jsb.reflection.callStaticMethod(
        "AnalyticsUtils",
        "trackEvent:",
        JSON.stringify({ name, properties }),
      );
    }
  }

  /**
   * 上报事件开始
   * @author Radish
   */
  public static trackEventDurationBegin(name: string) {
    if (cc.sys.isNative && cc.sys.os === cc.sys.OS_IOS) {
      jsb.reflection.callStaticMethod(
        "AnalyticsUtils",
        "trackEventDurationBegin:",
        JSON.stringify({ name }),
      );
    }
  }

  /**
   * 上报事件结束
   * @author Radish
   */
  public static trackEventDurationEnd(name: string) {
    if (cc.sys.isNative && cc.sys.os === cc.sys.OS_IOS) {
      jsb.reflection.callStaticMethod(
        "AnalyticsUtils",
        "trackEventDurationEnd:",
        JSON.stringify({ name }),
      );
    }
  }

  /**
   * 上报一个带时长的事件
   * @author Radish
   */
  public static trackEventWithDuration(name: string, duration: number) {
    if (cc.sys.isNative && cc.sys.os === cc.sys.OS_IOS) {
      jsb.reflection.callStaticMethod(
        "AnalyticsUtils",
        "trackEventWithDuration:",
        JSON.stringify({ name, duration }),
      );
    }
  }
}
