/**
 * 家园界面, 放置资源的控制栏
 * @author Radish
 */

import { INCREASE_DURATION, PLACE_PROP_ID } from "../../../../fairyland/Consts";
import State from "../../../../fairyland/State";
import {
  addMaxLaborForce,
  getLaborForce,
  getMaxLaborForce,
  subMaxLaborForce,
} from "../../../../fairyland/system/DeployLaborForceSystem";
import { getProp } from "../../../../fairyland/system/HandlePropSystem";
import IncreaseProgressBar from "./IncreaseProgressBar";
import LaborInfo from "./LaborInfo";
import PlacePropListCell from "./PlacePropListCell";

const { ccclass, property } = cc._decorator;
const DISTANCE = 396;
const DURATION = 0.2;

interface IArgs {
  onShowHandle: () => void;
  onHideHandle: () => void;
}

@ccclass
export default class PlacePropsToolbar extends cc.Component {
  @property(IncreaseProgressBar)
  public increaseProgressBar: IncreaseProgressBar = null;

  @property(LaborInfo)
  public laborInfo: LaborInfo = null;

  @property(cc.ScrollView)
  public scrollView: cc.ScrollView = null;

  @property(cc.Prefab)
  public placePropListCell: cc.Prefab = null;

  private m_isInit: boolean = false;
  private m_isMoving: boolean = false;
  private m_isDisplaying: boolean = false;
  private m_state: State = null;
  private m_args: IArgs = null;

  public async onDestroy() {
    if (this.m_state) {
      this.m_state.removeListener(
        "attribute_changed_maxLaborForce",
        this.updateLaborForce,
      );

      this.m_state.removeListener(
        "attribute_changed_laborForce",
        this.updateLaborForce,
      );

      this.m_state.removeListener(
        "attribute_changed_timestamp",
        this.updateProgressBar,
      );
    }
  }

  /**
   * 初始化
   * @author Radish
   */
  public init(args: IArgs, state: State) {
    if (this.m_isInit) {
      return;
    }
    this.m_isInit = false;

    this.updateLaborForce = this.updateLaborForce.bind(this);
    this.updateProgressBar = this.updateProgressBar.bind(this);

    this.m_args = args;
    this.m_state = state;

    // props
    let height = 0;
    PLACE_PROP_ID.forEach((value) => {
      const cell = cc.instantiate(this.placePropListCell);
      cell.y = -height;
      this.scrollView.content.addChild(cell);
      height += cell.height;

      const component = cell.getComponent(PlacePropListCell);
      component.initDisplay(getProp(state, value), state);
    });
    this.scrollView.content.height = height;

    // labor force
    this.updateLaborForce();

    // progress
    this.updateProgressBar();

    // events
    this.m_state.on("attribute_changed_maxLaborForce", this.updateLaborForce);
    this.m_state.on("attribute_changed_laborForce", this.updateLaborForce);
    this.m_state.on("attribute_changed_timestamp", this.updateProgressBar);
  }

  /**
   * 是否正在播放动画
   * @author Radish
   */
  public get isMoving() {
    return this.m_isMoving;
  }

  /**
   * 是否是展示状态
   * @author Radish
   */
  public get isDisplaying() {
    return this.m_isDisplaying;
  }

  /**
   * 显示
   * @author Radish
   */
  public show(other?: (duration: number) => void) {
    if (this.isMoving || this.isDisplaying) {
      return;
    }

    const duration = DURATION;

    this.m_isMoving = true;
    const seq = cc.sequence([
      cc.moveBy(duration, new cc.Vec2(-DISTANCE, 0)).easing(cc.easeSineOut()),
      cc.callFunc(() => {
        this.m_isMoving = false;
        this.m_isDisplaying = true;
      }),
    ]);
    this.node.runAction(seq);

    if (other) {
      other(duration);
    }
  }

  /**
   * 隐藏
   * @author Radish
   */
  public hide(other?: (duration: number) => void) {
    if (this.isMoving || !this.isDisplaying) {
      return;
    }

    const duration = DURATION;

    this.m_isMoving = false;
    const seq = cc.sequence([
      cc.moveBy(duration, new cc.Vec2(DISTANCE, 0)).easing(cc.easeSineOut()),
      cc.callFunc(() => {
        this.m_isMoving = false;
        this.m_isDisplaying = false;
      }),
    ]);
    this.node.runAction(seq);

    if (other) {
      other(duration);
    }
  }

  /**
   * 点击设置按钮
   * @author Radish
   */
  public onClickSetting() {
    if (this.isDisplaying) {
      this.m_args.onHideHandle();
    } else {
      this.m_args.onShowHandle();
    }
  }

  /**
   * 增加最大劳动力
   * @author Radish
   */
  public onClickPlusMaxLaborForce() {
    addMaxLaborForce(this.m_state, "1");
  }

  /**
   * 减少最大劳动力
   * @author Radish
   */
  public onClickMinusMaxLaborForce() {
    subMaxLaborForce(this.m_state, "1");
  }

  /**
   * 最大劳动力改变侦听事件
   * @author Radish
   */
  private updateLaborForce() {
    const laborForce = getLaborForce(this.m_state);
    const maxLaborForce = getMaxLaborForce(this.m_state);
    this.laborInfo.setLabor(laborForce, maxLaborForce);
  }

  /**
   * 更新进度条显示
   * @author Radish
   */
  private updateProgressBar() {
    this.increaseProgressBar.reset(
      this.m_state.origin.timestamp,
      INCREASE_DURATION,
    );
  }
}
