/**
 * 大厅放置增加时间条
 * @author Radish
 */

import { formatCountDownHHMMSS } from "../../../../fairyland/utils/Formatter";

const { ccclass, property } = cc._decorator;

@ccclass
export default class IncreaseProgressBar extends cc.Component {
  @property(cc.ProgressBar)
  public progressBar: cc.ProgressBar = null;

  @property(cc.Label)
  public label: cc.Label = null;

  private m_startTimestamp: number = 0;
  private m_duration: number = 0;

  public onLoad() {
    this.updateDisplay();
  }

  /**
   * 重置进度条参数
   * @author Radish
   */
  public reset(startTimestamp: number, duration: number) {
    this.m_startTimestamp = startTimestamp;
    this.m_duration = duration;
    this.updateDisplay();
  }

  public update(dt: number) {
    this.updateDisplay();
  }

  /**
   * 更新显示
   * @author Radish
   */
  private updateDisplay() {
    const defaultProgressValue = 0;
    const defaultLabelValue = "00:00:00";

    if (this.m_startTimestamp === 0 || this.m_duration === 0) {
      this.progressBar.progress = defaultProgressValue;
      this.label.string = defaultLabelValue;
    } else {
      const curTimestamp = Date.now();
      const curDuration = (curTimestamp - this.m_startTimestamp) / 1000;

      if (curDuration < 0) {
        this.progressBar.progress = defaultProgressValue;
        this.label.string = defaultLabelValue;
      } else {
        this.progressBar.progress = curDuration / this.m_duration;
        if (this.m_duration > curDuration) {
          this.label.string = formatCountDownHHMMSS(
            this.m_duration - Math.floor(curDuration),
          );
        } else {
          this.label.string = defaultLabelValue;
        }
      }
    }
  }
}
