/**
 * 本地存储相关处理
 * @author Radish
 */

import * as _ from "lodash";
import State from "../../fairyland/State";
import { producePlaceProps } from "../../fairyland/system/HandlePropSystem";
import { initOrigin } from "./InitOriginSystem";

const ACCOUNT = "account";
const VERSION = "1";

/**
 * 获得本地存储的账户
 * @author Radish
 */
export function getStorageAccount() {
  return cc.sys.localStorage.getItem(ACCOUNT + "-" + VERSION);
}

/**
 * 设置本地存储的账户数据
 * @author Radish
 */
export function setStorageAccount(account: string) {
  cc.sys.localStorage.setItem(ACCOUNT + "-" + VERSION, account);
}

/**
 * 删除本地缓存的账户信息
 * @author Radish
 */
export function delStorageAccount() {
  cc.sys.localStorage.removeItem(ACCOUNT + "-" + VERSION);
}

/**
 * 存储数据
 * @author Radish
 */
export function save(state: State) {
  cc.sys.localStorage.setItem(
    getOriginLocalStorageKey(state.origin.user.account),
    JSON.stringify(state.origin),
  );
}

/**
 * 读取数据
 * @author Radish
 */
export function load(account: string, state: State) {
  const jsonOrigin = cc.sys.localStorage.getItem(
    getOriginLocalStorageKey(account),
  );

  if (jsonOrigin) {
    state.origin = initOrigin(JSON.parse(jsonOrigin));
  }

  producePlaceProps(state);
}

/**
 * 获得本地存储的键
 * @author Radish
 */
function getOriginLocalStorageKey(account: string) {
  return account + "-origin-" + VERSION;
}
