/**
 * 格式化文本相关处理
 * @author Radish
 */

import Big from "big.js";
import {
  B_CONFINE_MAX,
  G_CONFINE_MAX,
  K_CONFINE_MAX,
  M_CONFINE_MAX,
} from "../Consts";

/**
 * 格式化道具当前数量文本
 * @author Radish
 */
export function formatPropNum(num: string): string {
  const bigNum = new Big(num);
  if (bigNum.lt(B_CONFINE_MAX)) {
    return new Big(num).toFixed(2);
  } else if (bigNum.lt(K_CONFINE_MAX)) {
    return bigNum.div(B_CONFINE_MAX).toFixed(2) + "K";
  } else if (bigNum.lt(M_CONFINE_MAX)) {
    return bigNum.div(K_CONFINE_MAX).toFixed(2) + "M";
  } else if (bigNum.lt(G_CONFINE_MAX)) {
    return bigNum.div(M_CONFINE_MAX).toFixed(2) + "G";
  } else {
    return bigNum.div(G_CONFINE_MAX).toFixed(2) + "T";
  }
}

/**
 * 补 0
 * @author Radish
 */
function paddingZero(num: string, paddingNum = 2) {
  let str = "";
  const max = paddingNum - num.length;

  for (let i = 0; i < max; ++i) {
    str += "0";
  }

  return str + num;
}

/**
 * 格式化秒的显示
 * 例如: 100 秒 => 00:01:40
 * @author Radish
 */
export function formatCountDownHHMMSS(sec: number): string {
  if (sec >= 3600) {
    const hour = sec % 3600;
    const min = (sec - hour * 3600) % 60;
    sec = sec - hour * 3600 - min * 60;
    return `${paddingZero(hour.toString())}:${paddingZero(
      min.toString(),
    )}:${paddingZero(sec.toString())}`;
  } else if (sec >= 60) {
    const min = sec % 60;
    sec = sec - min * 60;
    return `00:${paddingZero(min.toString())}:${paddingZero(sec.toString())}`;
  } else {
    return `00:00:${paddingZero(sec.toString())}`;
  }
}
