/**
 * 放置道具列表中的单元格
 * @author Radish
 */

import Big from "big.js";
import State from "../../../../fairyland/State";
import { getLaborForce } from "../../../../fairyland/system/DeployLaborForceSystem";
import {
  addLaborForceToProp,
  subLaborForceFromProp,
} from "../../../../fairyland/system/DeployLaborForceToPropSystem";
import { getProp } from "../../../../fairyland/system/HandlePropSystem";
import { IProp } from "../../../../fairyland/Types";
import { formatPropNum } from "../../../../fairyland/utils/Formatter";
import { loadSpriteFrame } from "../../../../sys/Utils";

const { ccclass, property } = cc._decorator;

@ccclass
export default class PlacePropListCell extends cc.Component {
  @property(cc.Sprite)
  public icon: cc.Sprite = null;

  @property(cc.Label)
  public propName: cc.Label = null;

  @property(cc.Label)
  public quantity: cc.Label = null;

  @property(cc.Label)
  public labor: cc.Label = null;

  @property(cc.Node)
  public btnPlusNode: cc.Node = null;

  @property(cc.Node)
  public btnMinusNode: cc.Node = null;

  @property(cc.SpriteFrame)
  public sfPlus: cc.SpriteFrame = null;

  @property(cc.SpriteFrame)
  public sfPlusF: cc.SpriteFrame = null;

  @property(cc.SpriteFrame)
  public sfMinus: cc.SpriteFrame = null;

  @property(cc.SpriteFrame)
  public sfMinusF: cc.SpriteFrame = null;

  @property(cc.ProgressBar)
  public progressBar: cc.ProgressBar = null;

  private m_isInit: boolean = false;
  private m_propId: string = null;
  private m_state: State = null;

  public onLoad() {
    this.onPropChanged = this.onPropChanged.bind(this);
    this.onLaborForceChanged = this.onLaborForceChanged.bind(this);
  }

  public onDestroy() {
    if (this.m_state) {
      this.m_state.removeListener(
        `attribute_changed_props[${this.m_propId}]`,
        this.onPropChanged,
      );

      this.m_state.removeListener(
        "attribute_changed_laborForce",
        this.onLaborForceChanged,
      );
    }
  }

  public update() {
    this.renderProgressBar(getProp(this.m_state, this.m_propId));
  }

  /**
   * 初始化初次展示
   * @author Radish
   */
  public initDisplay(prop: IProp, state: State) {
    if (this.m_isInit) {
      return;
    }
    this.m_isInit = true;

    this.m_propId = prop.id;
    this.m_state = state;

    this.icon.spriteFrame = null;

    const config = this.m_state.configs.props[prop.id];
    this.propName.string = config.name;

    this.renderQuantity(prop);
    this.renderIcon(prop);
    this.renderLabor(prop);
    this.renderButtons(prop, getLaborForce(this.m_state));
    this.renderProgressBar(prop);

    this.m_state.on(
      `attribute_changed_props[${this.m_propId}]`,
      this.onPropChanged,
    );

    this.m_state.on("attribute_changed_laborForce", this.onLaborForceChanged);
  }

  /**
   * 渲染数量
   * @author Radish
   */
  public renderQuantity(prop: IProp) {
    this.quantity.string = formatPropNum(prop.num);
  }

  /**
   * 渲染图标
   * @author Radish
   */
  public async renderIcon(prop: IProp) {
    const propLaborForce = new Big(prop.laborForce);
    const propLastNum = new Big(prop.lastNum);
    const propCurNum = new Big(prop.num);
    const delta = propCurNum.minus(propLastNum);

    let url = `props/Prop_${prop.id}_`;

    // 闲置
    if (propLaborForce.eq(0)) {
      const resource = await loadSpriteFrame(url + "N");
      if (resource) {
        this.icon.spriteFrame = resource;
      }
    } else {
      const result = delta.cmp(0);

      switch (result) {
        case 1: // 增长
          url += "R";
          break;

        case -1: // 减少
          url += "D";
          break;

        case 0: // 增减平衡
          url += "A";
          break;
      }

      const resource = await loadSpriteFrame(url);
      if (resource) {
        this.icon.spriteFrame = resource;
      }
    }
  }

  /**
   * 渲染劳动力数量
   * @author Radish
   */
  public renderLabor(prop: IProp) {
    this.labor.string = prop.laborForce;
  }

  /**
   * 渲染 +/- 按钮
   * @author Radish
   */
  public renderButtons(prop: IProp, laborForce: string) {
    const ownLaborForce = new Big(laborForce);
    const propLaborForce = new Big(prop.laborForce);

    // +, 可分配劳动力时, 红色; 无劳动力分配时灰色
    if (ownLaborForce.gt(0)) {
      const sprite = this.btnPlusNode.getComponent(cc.Sprite);
      const button = this.btnPlusNode.getComponent(cc.Button);

      sprite.spriteFrame = this.sfPlus;
      button.interactable = true;
    } else if (ownLaborForce.eq(0)) {
      const sprite = this.btnPlusNode.getComponent(cc.Sprite);
      const button = this.btnPlusNode.getComponent(cc.Button);

      sprite.spriteFrame = this.sfPlusF;
      button.interactable = false;
    }

    // -, 当劳动力为 0 时, 灰色; 有劳动力分配时红色
    if (propLaborForce.eq(0)) {
      const sprite = this.btnMinusNode.getComponent(cc.Sprite);
      const button = this.btnMinusNode.getComponent(cc.Button);

      sprite.spriteFrame = this.sfMinusF;
      button.interactable = false;
    } else if (propLaborForce.gt(0)) {
      const sprite = this.btnMinusNode.getComponent(cc.Sprite);
      const button = this.btnMinusNode.getComponent(cc.Button);

      sprite.spriteFrame = this.sfMinus;
      button.interactable = true;
    }
  }

  /**
   * 渲染进度条
   * @author Radish
   */
  public renderProgressBar(prop: IProp) {
    if (prop.laborForce > 0) {
      const config = this.m_state.configs.props[prop.id];
      const now = Date.now() / 1000;
      const timestamp = prop.startProduceTimestamp;
      const elapse = now - timestamp;
      if (elapse >= 0) {
        this.progressBar.progress = elapse / config.place.duration;
      } else {
        this.progressBar.progress = 0;
      }
    } else {
      this.progressBar.progress = 0;
    }
  }

  /**
   * 点击 + 按钮
   * @author Radish
   */
  public onClickPlus() {
    addLaborForceToProp(this.m_state, this.m_propId, "1");
  }

  /**
   * 点击 - 按钮
   * @author Radish
   */
  public onClickMinus() {
    subLaborForceFromProp(this.m_state, this.m_propId, "1");
  }

  /**
   * 道具改变
   * @author Radish
   */
  private onPropChanged(args: {
    route: string;
    value: IProp;
    lastValue?: IProp;
  }) {
    const { value, lastValue } = args;

    if (!lastValue || value.laborForce !== lastValue.laborForce) {
      this.renderLabor(value);
      this.renderIcon(value);
      this.renderProgressBar(value);
    }

    if (!lastValue || value.num !== lastValue.num) {
      this.renderQuantity(value);
    }
  }

  /**
   * 劳动力改变
   * @author Radish
   */
  private onLaborForceChanged(args: {
    route: string;
    value: string;
    lastValue?: string;
  }) {
    const { value, lastValue } = args;
    if (value !== lastValue) {
      this.renderButtons(
        getProp(this.m_state, this.m_propId),
        getLaborForce(this.m_state),
      );
    }
  }
}
