/**
 * 加载 cc.Prefab 资源
 * @author Radish
 */
export async function loadPrefab(url: string): Promise<any> {
  return new Promise((resolve, reject) => {
    cc.loader.loadRes(url, cc.Prefab, (err, resource) => {
      if (err) {
        if (CC_DEBUG) {
          cc.error(err);
        } else {
          reject();
        }
      } else {
        resolve(resource);
      }
    });
  });
}

/**
 * 加载 json 资源
 * @author Radish
 */
export async function loadJson(url: string): Promise<any> {
  return new Promise((resolve, reject) => {
    cc.loader.loadRes(url, (err, jsonAssets) => {
      if (err) {
        if (CC_DEBUG) {
          cc.error(err);
        } else {
          reject();
        }
      } else {
        resolve(jsonAssets.json);
      }
    });
  });
}

