/**
 * 警告弹窗
 * @author Radish
 */

const { ccclass, property } = cc._decorator;

@ccclass
export default class AlertViewComponent extends cc.Component {
  @property(cc.Label)
  public label: cc.Label = null;

  /**
   * 设置弹窗提示标题
   * @author Radish
   */
  public setTitle(title: string) {
    this.label.string = title;
  }

  /**
   * 点击关闭按钮
   * @author Radish
   */
  public onClickClose() {
    this.node.active = false;
  }
}
