/**
 * i18n 处理
 * @author Radish
 */

import NativeResponse from "../../native/NativeResponse";

export default class Localized {
  /**
   * 获得当前使用语言标识
   * @author Radish
   */
  public static async getLanguage() {
    if (!this.s_language) {
      if (cc.sys.isBrowser) {
        this.s_language = this.process(window.navigator.language);
        return Promise.resolve(this.s_language);
      }

      if (cc.sys.os === cc.sys.OS_IOS) {
        return new Promise((resolve) => {
          jsb.reflection.callStaticMethod(
            "QXQUtils",
            "getLanguage:",
            JSON.stringify({
              callback: NativeResponse.push((value: string) => {
                this.s_language = this.process(value);
                resolve(this.s_language);
              }),
            }),
          );
        });
      } else {
        this.s_language = "en";
        return Promise.resolve(this.s_language);
      }
    } else {
      return Promise.resolve(this.s_language);
    }
  }

  /**
   * 本地化文本内容, 异步
   * @author Radish
   */
  public static async getValue(key: string) {
    await this.loadLocalizedText();
    return this.getValueSync(key);
  }

  /**
   * 本地化文本内容, 同步
   * @author Radish
   */
  public static getValueSync(key: string) {
    let value = this.s_dict[key];
    if (!value) {
      value = this.s_defaultDict[key];
      if (!value) {
        return key;
      }
    }
    return value;
  }

  /**
   * 获得默认的 TTF 字体
   * @author Radish
   */
  public static async getDefaultTTF() {
    const language = await this.getLanguage();
    return language + "/fonts/default.ttf";
  }

  /**
   * 本地化 label 控件
   * @author Radish
   */
  public static async localizeLabel(label: cc.Label) {
    label.string = await this.getValue(label.string);
    return new Promise(async (resolve, reject) => {
      cc.loader.loadRes(await this.getDefaultTTF(), cc.Font, (err, font) => {
        if (err) {
          if (CC_DEBUG) {
            reject(err);
          } else {
            resolve();
          }
        } else {
          label.font = font;
          resolve();
        }
      });
    });
  }

  /**
   * 获得本地化资源
   * @author Radish
   */
  public static async getLocalizedRes(url: string) {
    const language = await this.getLanguage();
    return language + "/" + url;
  }

  private static s_language: string = null;
  private static s_dict: { [k: string]: string } = {};
  private static s_defaultDict: { [k: string]: string } = {};

  /**
   * 对浏览器/终端返回的语言字符串做处理
   * @author Radish
   */
  private static process(value: string): string {
    value = value.toLocaleLowerCase();
    if (value.indexOf("zh-hant") !== -1) {
      return "zh-hant";
    } else if (value.indexOf("zh-hans") !== -1) {
      return "zh-hans";
    } else if (value.indexOf("zh-tw") !== -1 || value.indexOf("zh-hk") !== -1) {
      return "zh-hant";
    } else if (value.indexOf("zh-cn") !== -1) {
      return "zh-hans";
    } else if (value === "zh") {
      return "zh-hans";
    } else {
      return "en";
    }
  }

  /**
   * 加载本地化的文本
   * @author Radish
   */
  private static async loadLocalizedText() {
    if (this.s_dict === null && this.s_defaultDict === null) {
      const language = await this.getLanguage();
      const p1 = new Promise(async (resolve, reject) => {
        cc.loader.loadRes(language + "/configs/Localized", (err, file) => {
          if (!err) {
            this.s_dict = file.json;
            resolve();
          } else {
            reject();
          }
        });
      });

      const p2 = new Promise((resolve, reject) => {
        cc.loader.loadRes(language + "/configs/Localized", (err, file) => {
          if (!err) {
            this.s_defaultDict = file.json;
            resolve();
          } else {
            reject();
          }
        });
      });
      return p1.then(() => p2).catch(() => p2);
    } else {
      return Promise.resolve();
    }
  }
}
