/**
 * 初始化配置相关功能
 * @author Radish
 */

import State from "../../fairyland/State";
import { IConfig } from "../../fairyland/Types";
import Localized from "../../sys/i18n/Localized";
import * as Utils from "../../sys/Utils";

/**
 * 初始化配置
 * @author Radish
 */
export async function initConfigs(state: State) {
  await initProps(state.configs);
}

/**
 * 初始化道具配置
 * @author Radish
 */
export async function initProps(config: IConfig) {
  const propConfigs = await Utils.loadJson(
    await Localized.getLocalizedRes("configs/Props"),
  );
  config.props = propConfigs;
}
