/**
 * 当前客户端的 App 实例
 * @author Radish
 */

import AlertViewComponent from "./app/ui/component/common/AlertViewComponent";

export default class App {
  /**
   * 单例模式, 获得 App 实例
   * @author Radish
   */
  public static getInstance() {
    if (this.s_instance === null) {
      this.s_instance = new App();
    }
    return this.s_instance;
  }

  private static s_instance: App = null;

  public main: cc.Node = null;

  private constructor() {
    if (App.s_instance !== null) {
      throw new Error("App already initialized.");
    }
  }

  /**
   * 获得 gameContainer 容器
   * @author Radish
   */
  public get gameContainer(): cc.Node {
    return this.main.getChildByName("gameContainer");
  }

  /**
   * 获得 sysContainer 容器
   * @author Radish
   */
  public get sysContainer(): cc.Node {
    return this.main.getChildByName("sysContainer");
  }

  /**
   * 显示弹窗
   * @author Radish
   */
  public showAlert(title: string) {
    const node = this.main.getChildByName("alertView");
    const component = node.getComponent(AlertViewComponent);
    component.setTitle(title);
    node.active = true;
  }
}
