/**
 * 数据相关处理内容
 * @author Radish
 */

import { EventEmitter } from "events";
import * as _ from "lodash";
import { IConfig, IOrigin } from "./Types";

interface IStateExtend {
  [k: string]: any;
}

export default class State extends EventEmitter implements IStateExtend {
  /**
   * 获得 State 实例
   * @author Radish
   */
  public static getInstance() {
    if (!this.s_instance) {
      this.s_instance = new State();
    }
    return this.s_instance;
  }
  private static s_instance: State;

  private m_origin: IOrigin = {
    laborForce: "0",
    props: {},
    user: {},
  };

  private m_configs: IConfig = {
    props: {},
  };

  private constructor() {
    super();

    if (State.s_instance) {
      throw new Error("State is already init.");
    }
  }

  /**
   * 设置 origin 值
   * @author Radish
   */
  public set<T>(route: string, value: T) {
    const lastValue = _.get(this.m_origin, route);
    _.set(this.m_origin, route, value);
    this.emit("attribute_changed", { route, value, lastValue });
    this.emit("attribute_changed_" + route, { route, value, lastValue });
  }

  /**
   * 获得 origin 值
   * @author Radish
   */
  public get<T>(route: string, defaultValue?: T): T {
    const value = _.get(this.m_origin, route);
    if ((value === null || value === undefined) && defaultValue !== undefined) {
      _.set(this.m_origin, route, defaultValue);
      return defaultValue;
    }
    return value;
  }

  /**
   * 获得 origin 实例
   * @author Radish
   */
  public get origin() {
    return this.m_origin;
  }

  /**
   * 设置 origin 实例
   * @author Radish
   */
  public set origin(value: IOrigin) {
    this.m_origin = value;
  }

  /**
   * 获得 configs 实例
   * @author Radish
   */
  public get configs() {
    return this.m_configs;
  }

  /**
   * 设置 configs 实例
   * @author Radish
   */
  public set configs(value: IConfig) {
    this.m_configs = value;
  }
}
