/**
 * 配置验证功能
 * @author Radish
 */

import { IConfig, IPropConfig } from "../Types";

/**
 * 走过每一个节点, 看那风风雨雨, 起起落落, 点点滴滴
 * @author Radish
 */
function walk(
  config: IConfig,
  propConfig: IPropConfig,
  path: string[],
): boolean {
  const value = path.find((element) => {
    return element === propConfig.id;
  });

  path.push(propConfig.id!);

  if (value) {
    return false;
  } else {
    if (propConfig.place && propConfig.place.dependencies) {
      for (const v of propConfig.place.dependencies) {
        if (!walk(config, config.props[v.id], path)) {
          return false;
        }
      }
    }
  }

  path.pop();
  return true;
}

/**
 * 检查是否有循环组合
 * @author Radish
 */
export function checkLoopDependent(config: IConfig) {
  for (const k in config.props) {
    if (config.hasOwnProperty(k)) {
      const path: string[] = [];
      if (!walk(config, config.props[k], path)) {
        return path;
      }
    }
  }
}
